import header2 from '@/components/header/header2';
export default {
    data() {
        return {
            sumMoney: '',
            totalPrice: 0,
            storeName: '',
            discountMoney: '',
            storeCode: this.$route.params.storeCode,
            showBox: false,
            verificationCode: '',
            orderNbr: ''
        };
    },
    computed: {
    },
    watch: {
        sumMoney(val) {
            if (val <= this.discountMoney) {
                this.totalPrice = 0;
            } else {
                this.totalPrice = val - this.discountMoney;
            }
        }
    },
    methods: {
        goBack () {
            this.$router.go(-1);
        },
        submitOrder() {
            if (this.totalPrice >= 500) {
                this.axios({
                    method: 'post',
                    url: '/Api/GuiBankPay',
                    data: '{"id":19,"jsonrpc":"2.0","method":"makeOrderAndPay","params":{"payAmount":"' + this.sumMoney + '","realPay":"' + this.totalPrice + '","userCode":"' + this.$store.state.userCode + '","payType":"normalPay","shopCode":"' + this.storeCode + '"}}',
                    contentType: 'application/json',
                    dataType: 'json'
                }).then((result) => {
                    if (result.data.result.status === 'success') {
                        this.orderNbr = result.data.result.orderNbr;
                        this.showBox = true;
                    } else {
                        this.$vux.toast.text('请求失败，请联系管理员', 'bottom');
                    }
                });
            } else {
                console.log('{"id":19,"jsonrpc":"2.0","method":"makeOrderAndPay","params":{"payAmount":"' + this.sumMoney + '","realPay":"' + this.totalPrice + '","userCode":"' + this.$store.state.userCode + '","payType":"freePay","shopCode":"' + this.storeCode + '"}}');
                this.axios({
                    method: 'post',
                    url: '/Api/GuiBankPay',
                    data: '{"id":19,"jsonrpc":"2.0","method":"makeOrderAndPay","params":{"payAmount":"' + this.sumMoney + '","realPay":"' + this.totalPrice + '","userCode":"' + this.$store.state.userCode + '","payType":"freePay","shopCode":"' + this.storeCode + '"}}',
                    contentType: 'application/json',
                    dataType: 'json'
                }).then((result) => {
                    if (result.data.result.status === 'success') {
                        this.$router.push({path: `/accounts`});
                        this.$vux.toast.text('支付成功', 'bottom');
                    } else {
                        this.$vux.toast.text('支付失败', 'bottom');
                    }
                });
            }
        },
        isShowBox(obj) {
            this.showBox = obj;
        },
        cancel() {
            this.axios({
                method: 'post',
                url: '/Api/GuiBankPay',
                data: '{"id":19,"jsonrpc":"2.0","method":"cancelPay","params":{"userCode":"' + this.$store.state.userCode + '", "orderNbr": "' + this.orderNbr + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                console.log(result);
                this.showBox = false;
                this.$vux.toast.text('已取消支付', 'bottom');
            });
        },
        submitForm() {
            this.axios({
                method: 'post',
                url: '/Api/GuiBankPay',
                data: '{"id":19,"jsonrpc":"2.0","method":"makeSurePay","params":{"checkCode":"' + this.verificationCode + '", "orderNbr": "' + this.orderNbr + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                if (result.data.result.status === 'success') {
                    this.showBox = false;
                    this.$router.push({path: `/accounts`});
                    this.$vux.toast.text('支付成功', 'bottom');
                } else {
                    this.showBox = false;
                    this.$vux.toast.text('支付失败', 'bottom');
                }
            });
        }
    },
    mounted() {
        this.$refs.sumImput.focus();
    },
    created() {
        this.storeCode = this.$route.params.storeCode;
        this.axios({
            method: 'post',
            url: '/Api/GuiBankPay',
            data: '{"id":19,"jsonrpc":"2.0","method":"getPayInfo","params":{"shopCode":"' + this.$route.params.storeCode + '"}}',
            contentType: 'application/json',
            dataType: 'json'
        }).then((result) => {
            if (result.data.result.status === 'success') {
                this.discountMoney = result.data.result.shopInfo.bankDiscount;
                this.storeName = result.data.result.shopInfo.shopName;
            } else {
            }
        });
    },
    components: {
        header2
    }
};



